:root {
  --primary-color: rgba(30, 58, 138, 0.8); /* Original background color */
  --secondary-color: #F59E0B;
  --background-gradient: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  --text-color: #FFFFFF;
  --input-bg: rgba(255, 255, 255, 0.1);
  --input-border: rgba(255, 255, 255, 0.3);
  --success-bg: rgba(46, 204, 113, 0.15);
  --error-bg: rgba(231, 76, 60, 0.15);
  
  /* Additional Variables for Enhanced Styling */
  --glow-color: #FFD700; /* Gold for glowing effects */
  --transition-duration: 0.4s;
  --border-radius: 16px;
  --shadow-light: rgba(0, 0, 0, 0.1);
  --shadow-medium: rgba(0, 0, 0, 0.2);
  --shadow-heavy: rgba(0, 0, 0, 0.4);
  --button-gradient: linear-gradient(135deg, #c0392b, #e74c3c);
  --focus-border-color: var(--secondary-color);
  --font-family: 'Inter', sans-serif;
}

* {
  box-sizing: border-box;
}

/* Main Container */
.deleteCwsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  font-family: var(--font-family);
  background: #1A202C;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

/* Overlay (Form Container) */
.deleteCwsOverlay {
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--border-radius);
  box-shadow: 0 10px 40px var(--shadow-medium);
  backdrop-filter: blur(12px);
  border: 1px solid var(--input-border);
  width: 100%;
  max-width: 500px;
  padding: 40px 50px;
  color: var(--text-color);
  animation: fadeIn 0.8s ease-out;
  box-sizing: border-box;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease;
  
  /* Custom Scrollbar Styling */
  
  /* Webkit Browsers (Chrome, Safari) */
  &::-webkit-scrollbar {
    width: 12px;
  }
  
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: var(--border-radius);
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: var(--secondary-color);
    border-radius: var(--border-radius);
    border: 3px solid rgba(255, 255, 255, 0.1);
    transition: background-color var(--transition-duration) ease, transform var(--transition-duration) ease;
  }
  
  &::-webkit-scrollbar-thumb:hover {
    background-color: var(--glow-color);
    transform: scale(1.1);
  }
  
  /* Firefox */
  scrollbar-width: thin;
  scrollbar-color: var(--secondary-color) rgba(255, 255, 255, 0.05);
}

/* Hover Effect for Overlay */
.deleteCwsOverlay:hover {
  transform: translateY(-8px);
  box-shadow: 0 25px 70px var(--shadow-heavy);
}

/* Fade-In Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Title Styling */
.deleteCwsTitle {
  text-align: center;
  font-size: 2.4rem;
  font-weight: 800;
  color: var(--secondary-color);
  margin-bottom: 30px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  position: relative;
  transition: color var(--transition-duration) ease;
}

.deleteCwsTitle::after {
  content: '';
  position: absolute;
  width: 90px;
  height: 4px;
  background: linear-gradient(90deg, #14B8A6, var(--secondary-color));
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
  transition: width 0.3s ease;
}

.deleteCwsTitle:hover {
  color: var(--glow-color);
}

.deleteCwsTitle:hover::after {
  width: 120px;
}

/* Form Container */
.formContainer {
  width: 100%;
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: var(--border-radius);
  padding: 30px 40px;
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 20px;
  transition: background 0.3s ease, box-shadow 0.3s ease;
}

.formContainer:hover {
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.4);
}

/* Form Label */
.formLabel {
  color: #FFFFFF;
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
}

/* Form Field */
.formField {
  width: 100%;
  padding: 14px 20px;
  font-size: 1rem;
  border: 1px solid var(--input-border);
  border-radius: var(--border-radius);
  background: rgba(255, 255, 255, 0.1);
  color: #FFFFFF;
  transition: all 0.3s ease;
  outline: none;
}

.formField::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.formField:focus {
  border-color: var(--focus-border-color);
  box-shadow: 0 0 8px var(--focus-border-color);
  background: black;
}

/* Delete Button */
.deleteSpaceBtn {
  padding: 14px 20px;
  font-size: 1.05rem;
  font-weight: 700;
  color: #FFFFFF;
  background: var(--button-gradient);
  border: none;
  border-radius: var(--border-radius);
  box-shadow: 0 6px 20px var(--shadow-medium);
  cursor: pointer;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease, background var(--transition-duration) ease;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.deleteSpaceBtn:hover {
  transform: translateY(-6px);
  box-shadow: 0 15px 35px var(--shadow-heavy);
}

.deleteSpaceBtn:active {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px var(--shadow-medium);
}

/* Ripple Effect on Button Click */
.deleteSpaceBtn::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.6s ease;
}

.deleteSpaceBtn:active::after {
  transform: scale(4);
  opacity: 1;
  transition: transform 0s ease, opacity 1s ease;
}

/* Success and Error Messages */
.successMessage,
.errorMessage {
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  padding: 14px 20px;
  border-radius: 8px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease, transform 0.3s ease;
  opacity: 1;
}

.successMessage {
  background: var(--success-bg);
  color: #2ECC71; /* Green */
  border: 1px solid rgba(46, 204, 113, 0.4);
  box-shadow: 0 4px 12px var(--shadow-light);
}

.errorMessage {
  background: var(--error-bg);
  color: #E74C3C; /* Red */
  border: 1px solid rgba(231, 76, 60, 0.4);
  box-shadow: 0 4px 12px var(--shadow-light);
}

/* Message Icon Styling */
.message-icon {
  margin-right: 10px;
  font-size: 1.3rem;
}

/* Responsive Design Enhancements */

/* Tablets and Small Desktops */
@media (max-width: 1024px) {
  .deleteCwsOverlay {
    max-width: 90%;
    padding: 35px 40px;
  }

  .deleteCwsTitle {
    font-size: 2.2rem;
    margin-bottom: 28px;
  }

  .formContainer {
    gap: 20px;
    padding: 25px 30px;
  }

  .formLabel {
    font-size: 0.95rem;
    margin-bottom: 6px;
  }

  .formField {
    font-size: 0.95rem;
    padding: 12px 18px;
  }

  .deleteSpaceBtn {
    font-size: 1rem;
    padding: 12px 18px;
  }

  .message-icon {
    font-size: 1.2rem;
  }
}

/* Mobile Devices */
@media (max-width: 768px) {
  .deleteCwsOverlay {
    max-width: 85%;
    padding: 30px 35px;
  }

  .deleteCwsTitle {
    font-size: 2rem;
    margin-bottom: 25px;
  }

  .formContainer {
    gap: 18px;
    padding: 20px 25px;
  }

  .formLabel {
    font-size: 0.9rem;
    margin-bottom: 5px;
  }

  .formField {
    font-size: 0.9rem;
    padding: 10px 16px;
  }

  .deleteSpaceBtn {
    font-size: 0.95rem;
    padding: 10px 16px;
  }

  .successMessage,
  .errorMessage {
    font-size: 0.95rem;
    padding: 12px 16px;
  }

  .message-icon {
    font-size: 1.1rem;
  }
}

/* Extra Small Devices */
@media (max-width: 480px) {
  .deleteCwsOverlay {
    max-width: 95%;
    padding: 25px 20px;
  }

  .deleteCwsTitle {
    font-size: 1.8rem;
    margin-bottom: 22px;
  }

  .formContainer {
    gap: 15px;
    padding: 15px 20px;
  }

  .formLabel {
    font-size: 0.85rem;
    margin-bottom: 4px;
  }

  .formField {
    font-size: 0.85rem;
    padding: 8px 14px;
  }

  .deleteSpaceBtn {
    font-size: 0.9rem;
    padding: 8px 14px;
  }

  .successMessage,
  .errorMessage {
    font-size: 0.85rem;
    padding: 10px 14px;
  }

  .message-icon {
    font-size: 1rem;
  }
}
