:root {
  --primary-color: #2B6CB0;
  --secondary-color: #F59E0B;
  --background-color: rgba(30, 58, 138, 0.8);
  --text-color: #F9FAFB;
  --input-bg: rgba(255, 255, 255, 0.1);
  --input-border: #374151;
  --success-bg: rgba(20, 184, 166, 0.2);
  --error-bg: rgba(192, 57, 43, 0.2);
  --glow-color: #FFD700;
  --transition-duration: 0.4s;
  --border-radius: 12px;
  --shadow-light: rgba(0, 0, 0, 0.1);
  --shadow-medium: rgba(0, 0, 0, 0.2);
  --shadow-heavy: rgba(0, 0, 0, 0.4);
  --button-gradient: linear-gradient(135deg, #14B8A6, #FBBF24);
  --label-color: rgba(255, 255, 255, 0.8);
  --focus-border-color: var(--secondary-color);
}

/* Main Container */
.mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1A202C;
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  color: var(--text-color);
  position: relative;
  overflow: hidden;
  transition: background 0.5s ease;
}

/* Form Wrapper */
.formWrapper {
  background: rgba(255, 255, 255, 0.08);
  padding: 60px 70px;
  border-radius: var(--border-radius);
  box-shadow: 0 10px 40px var(--shadow-medium);
  border: 1px solid rgba(30, 58, 138, 0.2);
  width: 100%;
  max-width: 500px;
  animation: fadeIn 1s ease-out;
  backdrop-filter: blur(10px);
  position: relative;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease;
}

.formWrapper:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 60px var(--shadow-heavy);
}

/* Fade-In Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Title */
.title {
  text-align: center;
  margin-bottom: 40px;
  color: var(--secondary-color);
  font-weight: 800;
  font-size: 2.5rem;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  position: relative;
  transition: color var(--transition-duration) ease;
}

.title::after {
  content: '';
  position: absolute;
  width: 100px;
  height: 4px;
  background: linear-gradient(to right, var(--secondary-color), #fbbf24);
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
  transition: width 0.3s ease;
}

.title:hover {
  color: var(--glow-color);
}

.title:hover::after {
  width: 120px;
}

/* Form */
.form {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

/* Input Group */
.inputGroup {
  position: relative;
  width: 100%;
}

/* Input Icons */
.inputIcon {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  color: var(--secondary-color);
  font-size: 1.2rem;
  transition: color var(--transition-duration) ease;
}

.inputGroup:hover .inputIcon {
  color: var(--glow-color);
}

/* Input Fields */
.inputField,
.selectField,
.textareaField {
  width: 100%;
  padding: 16px 16px 16px 50px;
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: var(--border-radius);
  color: #FFFFFF;
  font-size: 1rem;
  outline: none;
  transition: border-color var(--transition-duration) ease, box-shadow var(--transition-duration) ease, background var(--transition-duration) ease;
}

.inputField::placeholder,
.selectField::placeholder,
.textareaField::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.inputField:focus,
.selectField:focus,
.textareaField:focus {
  border-color: var(--focus-border-color);
  box-shadow: 0 0 8px var(--focus-border-color);
  background: black;
}

/* Textarea Field */
.textareaField {
  resize: vertical;
  min-height: 120px;
}

/* Floating Labels */
.label {
  position: absolute;
  top: 50%;
  left: 50px;
  transform: translateY(-50%);
  background: transparent;
  padding: 0 5px;
  color: var(--label-color);
  font-size: 0.95rem;
  pointer-events: none;
  transition: all 0.3s ease;
}

.inputField:focus + .label,
.inputField:not(:placeholder-shown) + .label,
.selectField:focus + .label,
.selectField:not(:placeholder-shown) + .label,
.textareaField:focus + .label,
.textareaField:not(:placeholder-shown) + .label {
  top: -10px;
  left: 40px;
  font-size: 0.8rem;
  color: #94a3b8;
  background: rgba(30, 58, 138, 0.8);
  border-radius: 4px;
}

/* Submit Button */
.submitBtn {
  background: var(--button-gradient);
  color: #FFFFFF;
  border: none;
  border-radius: var(--border-radius);
  padding: 18px 24px;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease, background var(--transition-duration) ease;
  box-shadow: 0 8px 20px rgba(20, 184, 166, 0.5);
  position: relative;
  overflow: hidden;
}

.submitBtn:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(20, 184, 166, 0.7);
}

.submitBtn:active {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(20, 184, 166, 0.6);
}

/* Ripple Effect on Button Click */
.submitBtn::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.6s ease;
}

.submitBtn:active::after {
  transform: scale(4);
  opacity: 1;
  transition: transform 0s ease, opacity 1s ease;
}

/* Success and Error Messages */
.successMessage,
.errorMessage {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  margin-top: 20px;
  animation: fadeInMessage 0.5s ease-out;
}

@keyframes fadeInMessage {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.successMessage {
  background: var(--success-bg);
  color: #14B8A6;
  box-shadow: 0 4px 12px var(--shadow-light);
}

.errorMessage {
  background: var(--error-bg);
  color: #C0392B;
  box-shadow: 0 4px 12px var(--shadow-light);
}

/* Responsive Design Enhancements */

/* Mobile Devices (Portrait & Landscape) */
@media screen and (max-width: 768px) {
  .formWrapper {
    padding: 40px 35px;
  }

  .title {
    font-size: 2rem;
    margin-bottom: 35px;
  }

  .inputField,
  .selectField,
  .textareaField {
    padding: 14px 16px 14px 45px;
  }

  .label {
    left: 40px;
  }

  .submitBtn {
    padding: 16px 20px;
    font-size: 1rem;
  }

  .mainContainer {
    padding: 10px;
  }
}

/* Large Devices (Desktops & Tablets) */
@media screen and (min-width: 769px) and (max-width: 1200px) {
  .formWrapper {
    padding: 50px 55px;
  }

  .title {
    font-size: 2.2rem;
    margin-bottom: 40px;
  }

  .inputField,
  .selectField,
  .textareaField {
    padding: 16px 18px 16px 50px;
  }

  .submitBtn {
    padding: 18px 24px;
    font-size: 1.05rem;
  }
}

/* Extra Large Devices (Large Desktops) */
@media screen and (min-width: 1201px) {
  .formWrapper {
    padding: 60px 70px;
    max-width: 600px; /* Slightly larger form on very large screens */
  }

  .title {
    font-size: 2.5rem;
    margin-bottom: 45px;
  }

  .inputField,
  .selectField,
  .textareaField {
    padding: 18px 20px 18px 55px;
  }

  .submitBtn {
    padding: 20px 28px;
    font-size: 1.1rem;
  }
}
