:root {
  --primary-color: #1E3A8A; /* Original background color */
  --secondary-color: #F6AD55;
  --background-gradient: radial-gradient(circle, #1e3a8a 0%, #111827 100%);
  --text-color: #FFFFFF;
  --input-bg: rgba(255, 255, 255, 0.15);
  --input-border: rgba(255, 255, 255, 0.3);
  --success-bg: rgba(46, 204, 113, 0.15);
  --error-bg: rgba(231, 76, 60, 0.15);
  
  /* Additional Variables for Enhanced Styling */
  --glow-color: #FFD700; /* Gold for glowing effects */
  --transition-duration: 0.4s;
  --border-radius: 12px;
  --shadow-light: rgba(0, 0, 0, 0.1);
  --shadow-medium: rgba(0, 0, 0, 0.2);
  --shadow-heavy: rgba(0, 0, 0, 0.4);
  --button-gradient: linear-gradient(135deg, #c0392b, #e74c3c);
  --focus-border-color: var(--secondary-color);
  --font-family: 'Inter', sans-serif;
}

/* Main Container */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh; /* Vertically centers the form */
  background: #1A202C;
  font-family: var(--font-family);
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

/* Overlay (Form Container) */
.overlay {
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--border-radius);
  box-shadow: 0 10px 40px var(--shadow-medium);
  backdrop-filter: blur(12px);
  border: 1px solid var(--input-border);
  width: 100%;
  max-width: 500px; /* Optimal form width */
  padding: 40px 50px;
  color: var(--text-color);
  animation: fadeIn 0.8s ease-out;
  box-sizing: border-box;
  max-height: 90vh; /* Prevents overflow */
  overflow-y: auto; /* Scrolls if content exceeds */
  position: relative;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease;
  
  /* Custom Scrollbar Styling */
  
  /* Webkit Browsers (Chrome, Safari) */
  &::-webkit-scrollbar {
    width: 12px;
  }
  
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: var(--border-radius);
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: var(--secondary-color);
    border-radius: var(--border-radius);
    border: 3px solid rgba(255, 255, 255, 0.1);
    transition: background-color var(--transition-duration) ease;
  }
  
  &::-webkit-scrollbar-thumb:hover {
    background-color: var(--glow-color);
  }
  
  /* Firefox */
  scrollbar-width: thin;
  scrollbar-color: var(--secondary-color) rgba(255, 255, 255, 0.05);
}

/* Hover Effect for Overlay */
.overlay:hover {
  transform: translateY(-8px);
  box-shadow: 0 25px 70px var(--shadow-heavy);
}

/* Fade-In Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Title Styling */
.title {
  text-align: center;
  font-size: 2.4rem;
  font-weight: 800;
  color: var(--secondary-color);
  margin-bottom: 30px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  position: relative;
  transition: color var(--transition-duration) ease;
}

.title::after {
  content: '';
  position: absolute;
  width: 90px;
  height: 4px;
  background: linear-gradient(90deg, #14B8A6, var(--secondary-color));
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
  transition: width 0.3s ease;
}

.title:hover {
  color: var(--glow-color);
}

.title:hover::after {
  width: 120px;
}

/* Form Styling */
.form {
  display: flex;
  flex-direction: column;
  gap: 25px;
  width: 100%;
}

/* Select Field Styling */
.selectField {
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: var(--border-radius);
  padding: 14px 20px;
  font-size: 1rem;
  color: #FFFFFF;
  appearance: none; /* Removes default arrow */
  background-image: url('data:image/svg+xml;charset=US-ASCII,<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6"><path fill="%23FFFFFF" d="M0 0l5 6 5-6z"/></svg>');
  background-repeat: no-repeat;
  background-position: right 20px center;
  background-size: 10px 6px;
  transition: all var(--transition-duration) ease;
  cursor: pointer;
}

.selectField:focus {
  border-color: var(--focus-border-color);
  box-shadow: 0 0 8px var(--focus-border-color);
  background: black;
}

/* Input and Textarea Fields Styling */
.inputField,
.textareaField {
  background: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: var(--border-radius);
  padding: 14px 20px;
  font-size: 1rem;
  color: #FFFFFF;
  transition: all var(--transition-duration) ease;
  box-sizing: border-box;
}

.inputField::placeholder,
.textareaField::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.inputField:focus,
.textareaField:focus {
  border-color: var(--focus-border-color);
  box-shadow: 0 0 8px var(--focus-border-color);
  background: black;
}

.textareaField {
  resize: vertical;
  min-height: 100px;
  max-height: 200px;
}

/* Submit Button Styling */
.submitBtn {
  background: var(--button-gradient);
  border: none;
  color: #FFFFFF;
  font-size: 1.05rem;
  font-weight: 700;
  padding: 14px 20px;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease, background var(--transition-duration) ease;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 6px 20px var(--shadow-medium);
  position: relative;
  overflow: hidden;
}

.submitBtn:hover {
  transform: translateY(-6px);
  box-shadow: 0 15px 35px var(--shadow-heavy);
}

.submitBtn:active {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px var(--shadow-medium);
}

/* Ripple Effect on Button Click */
.submitBtn::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.6s ease;
}

.submitBtn:active::after {
  transform: scale(4);
  opacity: 1;
  transition: transform 0s ease, opacity 1s ease;
}

/* Success and Error Messages */
.successMessage,
.errorMessage {
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  padding: 14px 20px;
  border-radius: 8px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease;
}

.successMessage {
  background: var(--success-bg);
  color: #2ECC71; /* Green */
  border: 1px solid rgba(46, 204, 113, 0.4);
  box-shadow: 0 4px 12px var(--shadow-light);
}

.errorMessage {
  background: var(--error-bg);
  color: #E74C3C; /* Red */
  border: 1px solid rgba(231, 76, 60, 0.4);
  box-shadow: 0 4px 12px var(--shadow-light);
}

/* Message Icon Styling */
.message_icon {
  margin-right: 10px;
  font-size: 1.3rem;
}

/* Responsive Design Enhancements */

/* Tablets and Small Desktops */
@media (max-width: 1024px) {
  .overlay {
    max-width: 100%;
    padding: 35px 40px;
  }

  .title {
    font-size: 2.2rem;
    margin-bottom: 28px;
  }

  .selectField,
  .inputField,
  .textareaField {
    font-size: 0.95rem;
    padding: 12px 18px;
  }

  .submitBtn {
    font-size: 1rem;
    padding: 12px 18px;
  }

  .message_icon {
    font-size: 1.2rem;
  }
}

/* Mobile Devices */
@media (max-width: 768px) {
  .overlay {
    max-width: 85%;
    padding: 30px 35px;
  }

  .title {
    font-size: 2rem;
    margin-bottom: 25px;
  }

  .selectField,
  .inputField,
  .textareaField {
    font-size: 0.9rem;
    padding: 10px 16px;
  }

  .submitBtn {
    font-size: 0.95rem;
    padding: 10px 16px;
  }

  .message_icon {
    font-size: 1.1rem;
  }
}

/* Extra Small Devices */
@media (max-width: 480px) {
  .overlay {
    max-width: 95%;
    padding: 25px 20px;
  }

  .title {
    font-size: 1.8rem;
    margin-bottom: 22px;
  }

  .selectField,
  .inputField,
  .textareaField {
    font-size: 0.85rem;
    padding: 8px 14px;
  }

  .submitBtn {
    font-size: 0.9rem;
    padding: 8px 14px;
  }

  .message_icon {
    font-size: 0.9rem;
  }
}
