.details-page-container {
  /* CSS Variables for Consistent and Maintainable Styling */
  --primary-color: #2B6CB0;
  --secondary-color: #F6AD55;
  --accent-color: #F59E0B;
  --background-gradient: radial-gradient(circle at top, rgba(30, 58, 138, 0.95), rgba(17, 24, 39, 1));
  --overlay-color: rgba(17, 24, 39, 0.7);
  --text-color: #FFFFFF;
  --description-color: #A0AEC0;
  --font-family: 'Inter', Arial, sans-serif;
  --border-radius: 20px;
  --transition-duration: 0.4s;
  --shadow-light: rgba(0, 0, 0, 0.1);
  --shadow-medium: rgba(0, 0, 0, 0.2);
  --shadow-heavy: rgba(0, 0, 0, 0.4);
  --button-gradient: linear-gradient(135deg, #14B8A6, #FBBF24);
  --glow-color: #FFD700;
  --animation-duration: 0.6s;

  /* Container Styling */
  font-family: var(--font-family);
  background: #1A202C;
  color: var(--text-color);
  padding: 60px 20px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  transition: background 0.5s ease;
}

/* Overlay for Enhanced Depth and Focus */
.details-page-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--overlay-color);
  z-index: 0;
  backdrop-filter: blur(10px);
}

/* Main Layout */
.details-layout {
  /* Flexbox Layout with Column Direction */
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column; /* Column layout ensures image above info */
  align-items: center;
  max-width: 1000px;
  width: 100%;
  background: rgba(45, 55, 72, 0.85); /* Semi-transparent for glassmorphism */
  border-radius: var(--border-radius);
  box-shadow: 0 25px 50px var(--shadow-medium);
  overflow: hidden;
  padding: 40px 30px;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease;
  backdrop-filter: blur(5px); /* Glassmorphism Effect */
}

/* Hover Effect for Layout */
.details-layout:hover {
  transform: translateY(-10px) scale(1.005);
  box-shadow: 0 35px 70px var(--shadow-heavy);
}

/* Image Container with 3D Perspective */
.space-image-container {
  width: 100%;
  max-width: 800px;
  margin-bottom: 40px;
  overflow: hidden;
  border-radius: var(--border-radius);
  position: relative;
  perspective: 1000px; /* Enables 3D Transformations */
}

/* Gradient Overlay on Image */
.space-image-container::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6), transparent);
  pointer-events: none;
}

/* Image Styling with Advanced Hover Effects */
.space-image {
  width: 100%;
  height: auto;
  display: block;
  border-radius: var(--border-radius);
  transition: transform var(--transition-duration) ease, filter var(--transition-duration) ease;
  transform: scale(1) rotateY(0deg);
  transform-style: preserve-3d;
}

.space-image:hover {
  transform: scale(1.08) rotateY(5deg);
  filter: brightness(1.1) contrast(1.05);
}

/* Details Container with Fade-In Animation */
.space-details-container {
  width: 100%;
  padding: 20px;
  text-align: center;
  animation: fadeInUp var(--animation-duration) ease forwards;
}

/* Keyframes for Fade-In Up Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Title Styling with Animated Underline */
.space-details-container h1 {
  font-size: 3rem;
  color: var(--secondary-color);
  margin-bottom: 25px;
  font-weight: 800;
  line-height: 1.2;
  position: relative;
  transition: color var(--transition-duration) ease;
  cursor: pointer;
}

.space-details-container h1::after {
  content: '';
  position: absolute;
  width: 0;
  height: 3px;
  background: var(--secondary-color);
  left: 50%;
  bottom: -5px;
  transition: all 0.3s ease;
}

.space-details-container h1:hover {
  color: var(--glow-color); /* Subtle Gold Hue on Hover */
}

.space-details-container h1:hover::after {
  width: 50%;
  left: 25%;
}

/* Paragraph Styling with Custom Bullet */
.space-details-container p {
  font-size: 1.2rem;
  line-height: 1.8;
  margin-bottom: 18px;
  font-weight: 500;
  position: relative;
  transition: color var(--transition-duration) ease;
}

.space-details-container p::before {
  content: '';
  color: var(--secondary-color);
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

.space-details-container p:hover {
  color: #CBD5E0; /* Lighten Text on Hover for Emphasis */
}

/* Price Styling with Neon Glow Effect */
.space-details-container .space-price {
  font-size: 2.2rem;
  color: var(--secondary-color);
  font-weight: 700;
  margin-top: 30px;
  position: relative;
  transition: color var(--transition-duration) ease, text-shadow 0.3s ease;
}

.space-details-container .space-price::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  background: var(--secondary-color);
  bottom: -10px;
  left: 0;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.space-details-container .space-price:hover {
  color: var(--glow-color);
  text-shadow: 0 0 10px var(--glow-color), 0 0 20px var(--glow-color), 0 0 30px var(--glow-color);
}

.space-details-container .space-price:hover::after {
  transform: scaleX(1);
}

/* Description Styling with Text Gradient */
.space-details-container .space-description {
  font-size: 1.1rem;
  color: var(--description-color);
  margin-bottom: 25px;
  line-height: 1.6;
  background: linear-gradient(90deg, var(--secondary-color), var(--accent-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: background 0.3s ease;
}



/* Button Styling with Interactive Waves */
.configure-button {
  /* Base Styles */
  background: var(--button-gradient);
  color: #FFFFFF;
  padding: 18px 70px;
  border: none;
  cursor: pointer;
  margin-top: 35px;
  border-radius: var(--border-radius);
  font-size: 1.15rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease, background 0.4s ease;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  display: inline-block;
  z-index: 0;
}

.configure-button::before,
.configure-button::after {
  /* Pseudo-elements for Interactive Effects */
  content: '';
  position: absolute;
  width: 300%;
  height: 300%;
  top: 50%;
  left: 50%;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 40%;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: all 0.6s ease;
  opacity: 0;
  z-index: -1;
}

.configure-button:hover::before,
.configure-button:hover::after {
  /* Active States for Pseudo-elements */
  transform: translate(-50%, -50%) rotate(0deg);
  opacity: 1;
}

.configure-button:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
}

.configure-button:active {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
}

/* Ripple Effect on Button Click */
.configure-button::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.6s ease;
}

.configure-button:active::after {
  transform: scale(4);
  opacity: 1;
  transition: transform 0s ease, opacity 1s ease;
}

/* Loading and Error Messages Styling */
.loading-message,
.error-message {
  /* Container Styles */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: var(--text-color);
  font-size: 1.4rem;
  font-weight: 600;
  padding: 35px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: var(--border-radius);
  background: rgba(246, 173, 85, 0.25);
  box-shadow: 0 5px 20px var(--shadow-light);
  max-width: 550px;
  margin: 50px auto;
  z-index: 1;
  position: relative;
  backdrop-filter: blur(5px);
}

/* Decorative SVG Elements */
.decorative-svg {
  position: absolute;
  top: 10%;
  right: 10%;
  width: 100px;
  height: 100px;
  opacity: 0.2;
  pointer-events: none;
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(10deg);
  }
  100% {
    transform: translateY(0px) rotate(-10deg);
  }
}

/* Responsive Design Enhancements */

/* Maintain Column Layout Across All Screens */
@media (max-width: 480px),
       (min-width: 481px) and (max-width: 768px),
       (min-width: 769px) and (max-width: 1024px),
       (min-width: 1025px) {
  .details-layout {
    flex-direction: column; /* Ensures column layout on all screens */
  }

  .space-image-container,
  .space-details-container {
    width: 100%;
  }

  /* Adjustments for Large Devices */
  @media (min-width: 1025px) {
    .space-image-container {
      padding-bottom: 20px;
    }

    .space-details-container {
      text-align: center;
      padding-left: 0;
    }
  }
}
