:root {
  --primary-color: #2B6CB0; /* Deep Blue */
  --secondary-color: #F6AD55; /* Warm Orange */
  --background-color: #EDF2F7; /* Light Gray */
  --text-color: #2D3748; /* Dark Gray */
  --font-family: 'Inter', sans-serif;

  --glow-color: #FFD700;
  --transition-duration: 0.4s;
  --border-radius: 15px;
  --shadow-light: rgba(0, 0, 0, 0.1);
  --shadow-medium: rgba(0, 0, 0, 0.2);
  --shadow-heavy: rgba(0, 0, 0, 0.4);
  --button-gradient: linear-gradient(135deg, #c0392b, #e74c3c);
  --input-bg: rgba(255, 255, 255, 0.1);
  --input-border: #374151;
  --success-bg: rgba(20, 184, 166, 0.2);
  --error-bg: rgba(192, 57, 43, 0.2);
}

/* Main Container */
.main-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  width: 100%;
  min-height: 100vh;
  padding: 30px 20px;
  font-family: var(--font-family);
  background: #1A202C;
  position: relative;
  overflow: hidden;
}

/* Card Container */
.card-container {
  width: 100%;
  max-width: 800px;
  padding: 40px;
  background: rgba(45, 55, 72, 0.95);
  border-radius: var(--border-radius);
  box-shadow: 0 15px 50px var(--shadow-medium);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #ffffff;
  animation: fadeIn 0.8s ease-out;
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease;
}

/* Hover Effect for Card Container */
.card-container:hover {
  transform: translateY(-8px) scale(1.01);
  box-shadow: 0 25px 70px var(--shadow-heavy);
}

/* Fade-In Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Title Styling */
.title {
  text-align: center;
  margin-bottom: 30px;
  color: var(--secondary-color);
  font-size: 2.8rem;
  font-weight: 800;
  letter-spacing: 2px;
  text-transform: uppercase;
  position: relative;
  transition: color var(--transition-duration) ease;
}

.title::after {
  content: '';
  position: absolute;
  width: 120px;
  height: 4px;
  background: linear-gradient(to right, #fbbf24, #f59e0b);
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 5px;
  transition: width 0.3s ease;
}

.title:hover {
  color: var(--glow-color);
}

.title:hover::after {
  width: 140px;
}

/* Reservation Count */
.reservation-count {
  text-align: center;
  margin-bottom: 25px;
  font-size: 1.3rem;
  color: var(--text-color);
}

.strong {
  font-weight: 700;
  color: var(--secondary-color);
}

/* Loader Styling */
.loader {
  text-align: center;
  font-size: 1.6rem;
  color: #3b82f6;
  margin: 25px 0;
  animation: pulse 1.5s infinite;
}

/* Pulse Animation for Loader */
@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

/* Error Message Styling */
.error {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  padding: 18px 25px;
  border-radius: 10px;
  background: var(--error-bg);
  color: #c0392b;
  font-weight: 600;
  font-size: 1.1rem;
  box-shadow: 0 4px 12px var(--shadow-light);
  animation: shake 0.5s ease;
}

/* Shake Animation for Error */
@keyframes shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  50% { transform: translateX(5px); }
  75% { transform: translateX(-5px); }
  100% { transform: translateX(0); }
}

/* Message Icon */
.message-icon {
  margin-right: 12px;
  font-size: 1.4rem;
}

/* Unordered List Styling */
.ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Individual Card Styling */
.card {
  background: linear-gradient(135deg, rgba(45, 55, 72, 1), rgba(45, 55, 72, 0.9));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--border-radius);
  padding: 30px;
  margin-bottom: 25px;
  color: #ffffff;
  box-shadow: 0 6px 20px var(--shadow-light);
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 30px var(--shadow-medium);
}

/* Paragraph Styling within Cards */
.card p {
  margin-bottom: 12px;
  font-size: 1.1rem;
  line-height: 1.6;
}

.card p span {
  font-weight: 700;
  color: var(--secondary-color);
}

/* Button Container */
.btn-container {
  display: flex;
  justify-content: flex-end;
  padding-top: 15px;
}

/* Delete Button Styling */
.delete-btn {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  background: var(--button-gradient);
  border: none;
  border-radius: var(--border-radius);
  color: #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 2px 4px 10px var(--shadow-light);
  transition: transform var(--transition-duration) ease, box-shadow var(--transition-duration) ease, background var(--transition-duration) ease;
}

.delete-btn:hover {
  background: linear-gradient(135deg, #e74c3c, #c0392b);
  transform: scale(1.05);
  box-shadow: 0 6px 15px var(--shadow-medium);
}

.delete-btn:active {
  transform: scale(0.98);
  box-shadow: 0 4px 10px var(--shadow-light);
}

/* Delete Icon Styling */
.delete-icon {
  margin-right: 8px;
  font-size: 1.2rem;
}

/* No Reservations Message */
.no-reservations {
  text-align: center;
  color: #9ca3af;
  font-size: 1.3rem;
  padding: 30px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 12px var(--shadow-light);
  animation: fadeInMessage 0.5s ease-out;
}

/* Fade-In Animation for Messages */
@keyframes fadeInMessage {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design Enhancements */

/* Mobile Devices (Portrait & Landscape) */
@media screen and (max-width: 768px) {
  .card-container {
    max-width: 95%;
    padding: 30px 25px;
  }

  .title {
    font-size: 2.2rem;
    margin-bottom: 28px;
  }

  .reservation-count {
    font-size: 1.2rem;
  }

  .card {
    padding: 25px;
  }

  .delete-btn {
    padding: 10px 20px;
    font-size: 0.95rem;
  }

  .loader {
    font-size: 1.4rem;
  }

  .error {
    padding: 14px 20px;
    font-size: 1rem;
  }

  .message-icon {
    font-size: 1.2rem;
  }
}

/* Tablet Devices */
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .card-container {
    max-width: 85%;
    padding: 35px 30px;
  }

  .title {
    font-size: 2.4rem;
    margin-bottom: 30px;
  }

  .reservation-count {
    font-size: 1.25rem;
  }

  .card {
    padding: 28px;
  }

  .delete-btn {
    padding: 12px 22px;
    font-size: 1rem;
  }

  .loader {
    font-size: 1.5rem;
  }

  .error {
    padding: 16px 22px;
    font-size: 1.05rem;
  }

  .message-icon {
    font-size: 1.25rem;
  }
}

/* Large Devices (Desktops & Large Tablets) */
@media screen and (min-width: 1025px) {
  .card-container {
    max-width: 80%;
    padding: 40px 35px;
  }

  .title {
    font-size: 2.6rem;
    margin-bottom: 32px;
  }

  .reservation-count {
    font-size: 1.3rem;
  }

  .card {
    padding: 30px;
  }

  .delete-btn {
    padding: 14px 24px;
    font-size: 1.05rem;
  }

  .loader {
    font-size: 1.6rem;
  }

  .error {
    padding: 18px 24px;
    font-size: 1.1rem;
  }

  .message-icon {
    font-size: 1.3rem;
  }
}
