/* ============================
   Color Palette
============================= */
:root {
  /* Original Color Variables */
  --color-primary: #2B6CB0; /* Rich Blue */
  --color-primary-light: #63B3ED; /* Sky Blue */
  --color-secondary: #F6AD55; /* Warm Orange */
  --color-secondary-light: #FBD38D; /* Light Peach */
  --color-accent: #48BB78; /* Green Teal */
  --color-background: #1A202C; /* Dark Background */
  --color-card: #FFFFFF; /* Pure White */
  --color-overlay: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  --color-text: #2D3748; /* Deep Slate */
  --color-text-light: #E2E8F0; /* Off-White Text */
  --color-error: #E53E3E; /* Vibrant Red */

  /* Additional Variables for Enhanced Styling */
  --font-family-primary: 'Inter', sans-serif;
  --font-family-secondary: 'Merriweather', serif;
  --font-size-base: 16px;
  --font-size-sm: 0.875rem;
  --font-size-md: 1rem;
  --font-size-lg: 1.5rem;
  --font-size-xl: 2rem;
  --font-size-xxl: 3rem;
  --font-weight-normal: 400;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extrabold: 900;

  --max-width: 1200px;
  --padding: 20px;
  --padding-lg: 40px;
  --padding-xl: 60px;
  --border-radius: 16px;
  --border-radius-lg: 24px;
  --container-gap: 30px;

  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 4px 20px rgba(0, 0, 0, 0.15);
  --shadow-lg: 0 12px 30px rgba(0, 0, 0, 0.2);
  --shadow-hover: 0 20px 40px rgba(0, 0, 0, 0.3);

  --transition-fast: 0.2s ease-in-out;
  --transition-medium: 0.3s ease-in-out;
  --transition-slow: 0.5s ease-in-out;
  --animation-duration: 0.6s;
  --animation-ease: cubic-bezier(0.4, 0, 0.2, 1);

  --z-index-overlay: 10;
  --z-index-modal: 1000;
  --z-index-dropdown: 500;

  --breakpoint-xs: 480px;
  --breakpoint-sm: 600px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --breakpoint-xxl: 1400px;
}

/* ============================
   Dark Mode Variables
============================= */
@media (prefers-color-scheme: dark) {
  :root {
    --color-background: #1A202C;
    --color-card: #2D3748;
    --color-text: #E2E8F0;
    --color-overlay: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  }
}

/* ============================
   Global Styles
============================= */
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: var(--font-size-base);
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family-primary);
  background: var(--color-background);
  color: var(--color-text);
  line-height: 1.6;
  transition: background-color var(--transition-medium), color var(--transition-medium);
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
  transition: color var(--transition-fast);
}

a:hover, a:focus {
  color: var(--color-primary-light);
}

img {
  max-width: 100%;
  display: block;
  border-radius: var(--border-radius);
  transition: transform var(--transition-fast);
}

button {
  font-family: inherit;
  cursor: pointer;
  border: none;
  background: none;
  transition: background var(--transition-fast), transform var(--transition-fast);
}

button:focus {
  outline: 3px solid var(--color-secondary);
  outline-offset: 2px;
}

hr {
  border: none;
  border-top: 1px solid var(--color-secondary-light);
  margin: 40px 0;
}

/* ============================
   Container
============================= */
.home-page-container {
  padding: var(--padding-xl) var(--padding);
  max-width: var(--max-width);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

/* ============================
   Center Content
============================= */
.home-content-center {
  text-align: center;
  width: 100%;
  animation: fadeIn 0.5s ease-in-out forwards;
  opacity: 0;
}

/* ============================
   Titles
============================= */
.home-title {
  font-family: var(--font-family-secondary);
  font-size: var(--font-size-xxl);
  color: var(--color-secondary);
  margin-bottom: 40px;
  position: relative;
  font-weight: var(--font-weight-extrabold);
  line-height: 1.2;
  animation: fadeInDown var(--animation-duration) var(--animation-ease) forwards;
  opacity: 0;
}

.home-title::after {
  content: '';
  width: 150px;
  height: 6px;
  background: linear-gradient(90deg, var(--color-primary), var(--color-secondary));
  display: block;
  margin: 20px auto 0;
  border-radius: 3px;
}

.home-section-title {
  font-family: var(--font-family-secondary);
  font-size: var(--font-size-xl);
  color: var(--color-secondary);
  margin-bottom: 30px;
  position: relative;
  font-weight: var(--font-weight-bold);
  animation: fadeInUp var(--animation-duration) var(--animation-ease) forwards;
  opacity: 0;
}

.home-section-title::after {
  content: '';
  width: 120px;
  height: 4px;
  background: linear-gradient(90deg, var(--color-primary-light), var(--color-secondary-light));
  display: block;
  margin: 15px auto 0;
  border-radius: 2px;
}

/* ============================
   Messages
============================= */
.home-loading,
.home-error-message,
.home-no-spaces {
  font-size: var(--font-size-md);
  margin: 25px 0;
  animation: fadeIn var(--animation-duration) var(--animation-ease) forwards;
  opacity: 0;
}

.home-error-message {
  color: var(--color-error);
}

/* ============================
   Slider Container
============================= */
.home-slider-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 80px;
  width: 100%;
  max-width: 100%;
  gap: 20px;
}

/* ============================
   Arrow Buttons
============================= */
.home-arrow {
  background: var(--color-card);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  box-shadow: var(--shadow-md);
  transition: background var(--transition-medium), transform var(--transition-medium), box-shadow var(--transition-medium);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: var(--color-primary);
  position: relative;
  z-index: 5;
  overflow: hidden;
}

.home-arrow:hover {
  background: linear-gradient(135deg, var(--color-primary), var(--color-secondary));
  color: var(--color-text-light);
  transform: translateY(-5px) scale(1.05);
  box-shadow: var(--shadow-lg);
}

.home-arrow:focus {
  outline: 3px solid var(--color-secondary);
  outline-offset: 4px;
}

.home-arrow.disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}

/* ============================
   Ripple Effect on Arrow Buttons
============================= */
.home-arrow::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.6s ease;
}

.home-arrow:active::after {
  transform: scale(4);
  opacity: 1;
  transition: transform 0s ease, opacity 1s ease;
}

/* ============================
   Spaces Slider
============================= */
.home-spaces-slider {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: var(--container-gap);
  overflow: hidden;
  width: 100%;
  transition: transform var(--transition-medium) ease-in-out;
  will-change: transform;
}

/* ============================
   Space Link
============================= */
.home-space-link {
  position: relative;
  display: block;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-sm);
  overflow: hidden;
  transition: transform var(--transition-medium), box-shadow var(--transition-medium), border-radius var(--transition-medium);
  will-change: transform;
  cursor: pointer;
  text-decoration: none;
}

.home-space-link:hover,
.home-space-link:focus-within {
  transform: translateY(-10px) scale(1.02);
  box-shadow: var(--shadow-hover);
  border-radius: var(--border-radius-lg);
}

/* ============================
   Space Image Container
============================= */
.home-space-image-container {
  position: relative;
  width: 100%;
  padding-top: 56.25%; /* 16:9 Aspect Ratio */
  overflow: hidden;
}

.home-space-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-fast);
}

.home-space-link:hover .home-space-image,
.home-space-link:focus-within .home-space-image {
  transform: scale(1.05);
}

/* ============================
   Space Hover Overlay
============================= */
.home-space-hover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-overlay);
  opacity: 0;
  transition: opacity var(--transition-fast), transform var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--z-index-overlay);
}

.home-space-link:hover .home-space-hover-overlay,
.home-space-link:focus-within .home-space-hover-overlay {
  opacity: 1;
  transform: scale(1.05);
}

/* ============================
   Space Hover Text
============================= */
.home-space-hover-text {
  color: var(--color-text-light);
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-bold);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity var(--transition-fast) 0.1s, transform var(--transition-fast);
}

.home-space-link:hover .home-space-hover-text,
.home-space-link:focus-within .home-space-hover-text {
  opacity: 1;
  transform: translateY(0);
  color: var(--color-text-light);
  font-weight: 900;
  text-shadow: 1px 1px 2px var(--color-background), 0 0 1em var(--color-primary), 0 0 0.2em var(--color-background);
}

/* ============================
   Space Info
============================= */
.home-space-info {
  padding: 25px;
  text-align: left;
  background: linear-gradient(180deg, rgba(6, 4, 4, 0.5) 0%, var(--color-card) 100%);
  transition: background var(--transition-medium);
}

.home-space-link:hover .home-space-info,
.home-space-link:focus-within .home-space-info {
  background: var(--color-card);
}

.home-space-name {
  font-family: var(--font-family-secondary);
  font-size: var(--font-size-xl);
  color: var(--color-secondary);
  margin-bottom: 10px;
  font-weight: var(--font-weight-bold);
  transition: color var(--transition-fast);
}

.home-space-link:hover .home-space-name,
.home-space-link:focus-within .home-space-name {
  color: var(--color-text-light);
}

.home-space-description {
  font-size: var(--font-size-md);
  color: var(--color-text);
  margin-bottom: 15px;
  line-height: 1.5;
  max-height: 4.5em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  transition: color var(--transition-fast);
}

.home-space-description::selection {
  background: var(--color-primary-light);
  color: var(--color-text-light);
}

/* ============================
   Decorative Dots
============================= */
.home-space-dots {
  display: flex;
  gap: 6px;
}

.home-space-dot {
  width: 10px;
  height: 10px;
  background-color: var(--color-secondary);
  border-radius: 50%;
  transition: background-color var(--transition-fast), transform var(--transition-fast);
}

.home-space-link:hover .home-space-dot,
.home-space-link:focus-within .home-space-dot {
  background-color: var(--color-primary);
  transform: scale(1.2);
}

/* ============================
   Artistic Section
============================= */
.home-artistic-section {
  margin-top: 80px;
  width: 100%;
}

.home-artistic-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--container-gap);
  transition: all var(--transition-medium) ease-in-out;
}

.home-artistic-card {
  position: relative;
  display: block;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-sm);
  overflow: hidden;
  transition: transform var(--transition-medium), box-shadow var(--transition-medium), border-radius var(--transition-medium);
  will-change: transform;
  cursor: pointer;
}

.home-artistic-card:hover,
.home-artistic-card:focus-within {
  transform: scale(1.02);
  box-shadow: var(--shadow-hover);
  border-radius: var(--border-radius-lg);
}

.home-artistic-image-container {
  position: relative;
  width: 100%;
  padding-top: 66.66%; /* 3:2 Aspect Ratio */
  overflow: hidden;
}

.home-artistic-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-fast);
}

.home-artistic-card:hover .home-artistic-image,
.home-artistic-card:focus-within .home-artistic-image {
  transform: scale(1.1);
}

.home-artistic-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: var(--color-text-light);
  padding: 25px;
  transform: translateY(100%);
  transition: transform var(--transition-medium);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home-artistic-card:hover .home-artistic-overlay,
.home-artistic-card:focus-within .home-artistic-overlay {
  transform: translateY(0);
}

.home-artistic-name {
  font-family: var(--font-family-secondary);
  font-size: var(--font-size-xl);
  margin-bottom: 10px;
  font-weight: var(--font-weight-extrabold);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  transition: text-shadow var(--transition-fast);
}

.home-artistic-description {
  font-size: var(--font-size-md);
  line-height: 1.5;
  max-height: 6em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

/* ============================
   Responsive Grid
============================= */
@media (min-width: 600px) {
  .home-artistic-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 992px) {
  .home-artistic-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1400px) {
  .home-artistic-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

/* ============================
   Responsive Adjustments
============================= */
@media (max-width: 1200px) {
  .home-page-container {
    padding: var(--padding-lg) var(--padding);
  }
}

@media (max-width: 992px) {
  .home-slider-container {
    margin-bottom: 60px;
  }
}

@media (max-width: 768px) {
  .home-slider-container {
    flex-direction: column;
    align-items: center;
  }

  .home-arrow {
    margin: 15px 0;
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
  }

  .home-spaces-slider {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    gap: 20px;
  }

  .home-space-link {
    margin: 0 auto;
  }

  .home-artistic-grid {
    grid-template-columns: 1fr;
  }

  /* Enhanced Mobile Styles */
  .home-title {
    font-size: 2.5rem;
  }

  .home-section-title {
    font-size: 2rem;
  }

  .home-space-name,
  .home-artistic-name {
    font-size: 1.5rem;
  }

  .home-space-description,
  .home-artistic-description {
    font-size: 0.95rem;
  }

  .home-space-hover-text {
    font-size: 1rem;
  }

  .home-space-dots {
    gap: 4px;
  }

  .home-space-dot {
    width: 8px;
    height: 8px;
  }
}

@media (max-width: 480px) {
  .home-title {
    font-size: 2rem;
  }

  .home-section-title {
    font-size: 1.75rem;
  }

  .home-space-name,
  .home-artistic-name {
    font-size: 1.3rem;
  }

  .home-space-description,
  .home-artistic-description {
    font-size: 0.9rem;
  }

  .home-arrow {
    width: 45px;
    height: 45px;
    font-size: 1.2rem;
  }

  .home-space-hover-text {
    font-size: 0.9rem;
  }
}

/* ============================
   Accessibility Enhancements
============================= */
.home-space-link:focus-within,
.home-artistic-card:focus-within {
  outline: 3px solid var(--color-secondary);
  outline-offset: 4px;
}

.home-arrow:focus {
  outline: 3px solid var(--color-secondary);
  outline-offset: 4px;
}

/* ============================
   Smooth Scroll Behavior
============================= */
html {
  scroll-behavior: smooth;
}

/* ============================
   Scrollbar Styling
============================= */
.home-spaces-slider::-webkit-scrollbar {
  width: 8px;
}

.home-spaces-slider::-webkit-scrollbar-track {
  background: var(--color-background);
}

.home-spaces-slider::-webkit-scrollbar-thumb {
  background-color: var(--color-primary-light);
  border-radius: 4px;
}

.home-spaces-slider::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-primary);
}

.home-spaces-slider {
  scrollbar-width: none; /* Firefox */
}

.home-spaces-slider::-webkit-scrollbar {
  display: none; /* Chrome, Safari */
}

/* ============================
   Utility Classes
============================= */
.hidden {
  display: none !important;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.fade-in {
  animation: fadeIn 0.5s ease-in-out forwards;
}

/* ============================
   Keyframes
============================= */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(42, 138, 228, 0.7);
  }
  70% {
    transform: scale(1.05);
    box-shadow: 0 0 0 10px rgba(42, 138, 228, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(42, 138, 228, 0);
  }
}

/* ============================
   Background Animations
============================= */
body::before {
  content: '';
  position: fixed;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(45deg, var(--color-primary), var(--color-secondary), var(--color-accent));
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
  z-index: -1;
  opacity: 0.1;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* ============================
   Hover Ripple Effect
============================= */
.home-space-link::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, var(--color-primary-light) 10%, transparent 10.01%);
  background-repeat: no-repeat;
  background-position: center;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0.4;
  transition: transform var(--transition-medium), opacity var(--transition-medium);
  pointer-events: none;
}

.home-space-link:hover::after,
.home-space-link:focus-within::after {
  transform: translate(-50%, -50%) scale(1);
  opacity: 0;
}

/* ============================
   Glassmorphism Effect
============================= */
.home-artistic-card,
.home-space-link {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* ============================
   3D Transform Effects
============================= */
.home-space-link:hover,
.home-space-link:focus-within {
  transform: perspective(1000px) rotateY(5deg) translateY(-10px) scale(1.02);
}

.home-artistic-card:hover,
.home-artistic-card:focus-within {
  transform: perspective(1000px) rotateY(-5deg) scale(1.02);
}

/* ============================
   Parallax Scrolling Effect
============================= */
.home-page-container {
  perspective: 1000px;
}

.home-content-center {
  transform: translateZ(0);
  transition: transform var(--transition-medium);
}

.home-page-container:hover .home-content-center {
  transform: translateZ(50px) rotateY(2deg);
}

/* ============================
   Animated Gradient Borders
============================= */
.home-space-link {
  position: relative;
  overflow: hidden;
}

.home-space-link::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  background: linear-gradient(45deg, var(--color-primary), var(--color-secondary), var(--color-accent));
  z-index: -1;
  border-radius: var(--border-radius);
  animation: gradientBorder 4s linear infinite;
  filter: blur(5px);
}

@keyframes gradientBorder {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* ============================
   Floating Elements
============================= */
@media (min-width: 1200px) {
  .home-floating-element {
    position: absolute;
    top: 20%;
    left: 80%;
    width: 100px;
    height: 100px;
    background: var(--color-accent);
    border-radius: 50%;
    box-shadow: var(--shadow-lg);
    animation: float 6s ease-in-out infinite;
    opacity: 0.7;
  }

  @keyframes float {
    0% {
      transform: translateY(0) rotate(0deg);
    }
    50% {
      transform: translateY(-20px) rotate(180deg);
    }
    100% {
      transform: translateY(0) rotate(360deg);
    }
  }
}

/* ============================
   Neon Glow Effect
============================= */
.home-space-name,
.home-artistic-name {
  position: relative;
  color: var(--color-secondary);
  text-shadow:
    0 0 1px var(--color-secondary-light),
    0 0 1px var(--color-secondary-light),
    0 0 1px var(--color-secondary),
    0 0 1px var(--color-secondary),
    0 0 1px var(--color-secondary);
  transition: text-shadow var(--transition-fast);
}

.home-space-link:hover .home-space-name,
.home-space-link:focus-within .home-space-name,
.home-artistic-card:hover .home-artistic-name,
.home-artistic-card:focus-within .home-artistic-name {
  text-shadow:
    0 0 1px var(--color-primary-light),
    0 0 1px var(--color-primary-light),
    0 0 1px var(--color-primary),
    0 0 1px var(--color-primary),
    0 0 1px var(--color-primary);
}

/* ============================
   Enhanced Button Styles with Ripple Effect
============================= */
.home-arrow {
  position: relative;
  overflow: hidden;
}

.home-arrow::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--transition-fast);
}

.home-arrow:active::after {
  opacity: 1;
  animation: ripple 0.4s forwards;
}

@keyframes ripple {
  from {
    transform: scale(0);
    opacity: 0.5;
  }
  to {
    transform: scale(4);
    opacity: 0;
  }
}

/* ============================
   Animated Underlines
============================= */
.home-space-name::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: var(--color-primary);
  transition: width var(--transition-medium);
  margin-top: 5px;
  border-radius: 2px;
}

.home-space-link:hover .home-space-name::after,
.home-space-link:focus-within .home-space-name::after {
  width: 100%;
}

/* ============================
   Advanced Button Styles
============================= */
.home-arrow {
  position: relative;
  overflow: hidden;
}

.home-arrow::before,
.home-arrow::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 200%;
  background: rgba(255, 255, 255, 0.1);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: opacity var(--transition-fast), transform var(--transition-fast);
  opacity: 0;
}

.home-arrow:hover::before,
.home-arrow:focus::before,
.home-arrow:hover::after,
.home-arrow:focus::after {
  opacity: 1;
  transform: translate(-50%, -50%) rotate(0deg);
}
